body {
  margin: 0;
  background-color: grey;
}

.grid-container {
  display: grid;
}

.products {
  display: flex;
  flex-wrap: wrap;
  grid-column: 1 / 3;
  padding: 20px;
  background-color: grey;
}

.productImage {
  border-radius: 20px;
  width: 200px;
  height: 200px;
}

.receipt {
  grid-column: 2 / 4;
  background-color: white;
}

.product {
  width: 33%;
  margin: auto;
  border: 1px solid black;
  margin-bottom: 4px;
  border-radius: 15px;
  background-color: rgb(173, 173, 173);
  display: flex;
  flex-direction: column;
  align-items: center;
}

header {
  width: 100%;
  background: black !important;
  color: aliceblue;
  font-size: 30px;
  padding: 10px;
}

.split-para {
  display: block;
  margin: 10px;
}

.split-para span {
  display: block;
  float: right;
  width: 50%;
  margin-left: 10px;
}